<script>
import FilterViewReportServices from "@/services/DataService";
import { ValidationObserver, Form, ValidationProvider as Field,extend, ErrorMessage } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
export default {
    setup(){
        Object.keys(rules).forEach(rule => {
            extend(rule, rules[rule]);
        });
    },
    components:{
        Form, Field, ValidationObserver
    },
    data() {
        return {
            isLoading: false,
            isFilterExist: 0,
            selectedOPValue: '',
            GetOperator: [],
            options: [],
            ValueDisplaytext: '',
            ValueDisplaytext2: '',
            ValueDisplaynumber: '',
            ValueDisplaynumber2: '',
            ValueDisplaydate: '',
            ValueDisplaydate2: '',
            modelFilterDDLValue: '',
            dropdwonvalues: [],
            texttypevalue1: false,
            texttypevalue2: false,
            numbertypevalue1: false,
            numbertypevalue2: false,
            datetypevalue1: false,
            datetypevalue2: false,
            multiselectvalue: false,
            DDLForFilter: false,
            SelectedoperatorMappedDatatype: '',
            CurrentOperatorSelected: '',
            savedOperatorName: '',
            operator_id: null,
            isBinding: false,
            ArrayforFilterpopup: [],
            FilterClose:false,
            FilterCategory:["AND","OR"],
            modelfiltertype:'AND',
            GetOperatorList: [],
        }
    },
    props: {
        reportListingInfo: {
            type: Object
        }
    },
    created() {
        var vm = this;
        vm.GetDropDownValueOnFilter();
    },
    watch:{
        FilterClose(newval){
            if(newval){
                this.GetDropDownValueOnFilter();
            }
        }
    },
    mounted: function () {
        $('#modalViewCustomReportFilter').modal({
            backdrop: 'static',
            keyboard: false
        })
    },
    methods: {
        close() {
            var vm = this;
            $("#modalViewCustomReportFilter").modal("hide"); $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
            this.$emit("close");
            //vm.FilterClose = true;
        },
        OpenFilterPopup(filterdata, event) {
            var vm = this;
            filterdata.selectedOPValue = '';
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            $(event.target).closest(".parentdiv").find(".filter-list-box").width('500px').css("overflow", "visible");
            vm.GetOperatordata(filterdata);
            vm.texttypevalue1 = false;
            vm.texttypevalue2 = false;
            vm.numbertypevalue1 = false;
            vm.datetypevalue1 = false;
            vm.datetypevalue2 = false;
            vm.multiselectvalue = false;
            vm.numbertypevalue2 = false;
            vm.DDLForFilter = false;
            vm.dropdwonvalues = [];
            vm.ValueDisplaytext = '';
            vm.ValueDisplaynumber = '';
            vm.ValueDisplaynumber2 = '';
            vm.ValueDisplaydate = '';
            vm.ValueDisplaydate2 = '';
        },
        ClosePopFilter() {
            var vm = this;
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            vm.texttypevalue1 = false;
            vm.texttypevalue2 = false;
            vm.numbertypevalue1 = false;
            vm.numbertypevalue2 = false;
            vm.datetypevalue1 = false;
            vm.datetypevalue2 = false;
            vm.multiselectvalue = false;
            vm.DDLForFilter = false;
            vm.multiselectvalue = false;
            vm.ValueDisplaytext = '';
            vm.ValueDisplaynumber = '';
            vm.ValueDisplaynumber2 = '';
            vm.ValueDisplaydate = '';
            vm.ValueDisplaydate2 = '';
            vm.dropdwonvalues = [];
        },
        resetthevalueofoperator: function () {
            var vm = this;
            vm.dropdwonvalues = [];
            vm.ValueDisplaytext = '';
            vm.ValueDisplaytext2 = '';
            vm.ValueDisplaynumber = '';
            vm.ValueDisplaynumber2 = '';
            vm.ValueDisplaydate = '';
            vm.ValueDisplaydate2 = '';
            vm.Displaymultiplevalue = ''
        },
        checkvalue: function (filterdata, OperatorID, OperatorDisplayName, OperatorExpression) {
            var vm = this;
            if (filterdata.ColumnFieldDataCode === 'select' && filterdata.PickListOption === 'Lookup') {
                vm.GetFilterSelectDDL(filterdata);
            }
            setTimeout(function () {
                vm.savedOperatorName = "";
                vm.SelectedoperatorMappedDatatype = filterdata.ColumnFieldDataCode;
                vm.CurrentOperatorSelected = OperatorExpression;
                vm.savedOperatorName = OperatorDisplayName;
                vm.operator_id = OperatorID

                if (vm.SelectedoperatorMappedDatatype == 'string') {
                    if (vm.savedOperatorName == 'Between') {
                        vm.texttypevalue1 = true;
                        vm.texttypevalue2 = true;
                    } else if (vm.savedOperatorName == 'Is Empty' || vm.savedOperatorName == 'Is Not Empty') {
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                    else {
                        vm.texttypevalue1 = true;
                        vm.texttypevalue2 = false;
                    }
                }
                else if (vm.SelectedoperatorMappedDatatype == 'decimal' || vm.SelectedoperatorMappedDatatype == 'int' || vm.SelectedoperatorMappedDatatype == 'bigint') {
                    if (vm.savedOperatorName == 'Between') {
                        vm.numbertypevalue1 = true;
                        vm.numbertypevalue2 = true;
                    } else if (vm.savedOperatorName == 'Is Empty' || vm.savedOperatorName == 'Is Not Empty') {
                        vm.numbertypevalue1 = false;
                        vm.numbertypevalue2 = false;
                    }
                    else {
                        vm.numbertypevalue1 = true;
                        vm.numbertypevalue2 = false;
                    }
                }
                else if (vm.SelectedoperatorMappedDatatype === 'select' && filterdata.PickListOption === 'Lookup') {
                    if (vm.savedOperatorName == 'Is Empty' || vm.savedOperatorName == 'Is Not Empty') {
                        vm.DDLForFilter = false;
                        vm.multiselectvalue = false;
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                    else if (vm.savedOperatorName == 'Equal to one of multiple possible values' || vm.savedOperatorName == 'Not Equal to one of multiple possible values') {
                        vm.multiselectvalue = true;
                        vm.DDLForFilter = false;
                    } else {
                        vm.DDLForFilter = true;
                        vm.multiselectvalue = false;
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                }
                else if (vm.SelectedoperatorMappedDatatype === 'date') {
                    if (vm.savedOperatorName == 'Between') {
                        vm.datetypevalue1 = true;
                        vm.datetypevalue2 = true;
                    } else if (vm.savedOperatorName == 'Is Empty' || vm.savedOperatorName == 'Is Not Empty' || vm.savedOperatorName == 'Is Off Day' || vm.savedOperatorName == 'Is Week End') {
                        vm.datetypevalue1 = false;
                        vm.datetypevalue2 = false;
                    }
                    else {
                        vm.datetypevalue1 = true;
                        vm.datetypevalue2 = false;
                    }
                }
                if (vm.operator_id != undefined && vm.savedOperatorName != undefined) {
                    vm.dropdwonvalues = [];
                    vm.ValueDisplaytext = '';
                    vm.ValueDisplaytext2 = '';
                    vm.ValueDisplaynumber = '';
                    vm.ValueDisplaynumber2 = '';
                    vm.ValueDisplaydate = '';
                    vm.ValueDisplaydate2 = '';
                    vm.modelFilterDDLValue = '';
                    if (filterdata.OPERATOR_ID == vm.operator_id) {
                        vm.FillFilterValues(OperatorID, filterdata);
                    }
                    // filterdata = [];
                }
                if (filterdata.OPERATOR_ID != vm.operator_id) {
                    vm.resetthevalueofoperator();
                }
            }, 1000);
        },
        FillFilterValues(id, filterdata) {
            var vm = this;
            vm.ArrayforFilterpopup.push(filterdata);
            var checkIfExists = vm.ArrayforFilterpopup.findIndex((x) => {
                return x.OPERATOR_ID == id
            });
            if (checkIfExists > -1) {
                var filtervalueexist = vm.ArrayforFilterpopup.find(x => x.OPERATOR_ID == id)
                if (filterdata.ColumnFieldDataCode == 'string') {
                    vm.ValueDisplaytext = filtervalueexist?.Value1;
                    vm.ValueDisplaytext2 = filtervalueexist?.Value2;
                }
                else if (filterdata.ColumnFieldDataCode == 'decimal' || filterdata.ColumnFieldDataCode == 'int' || filterdata.ColumnFieldDataCode == 'bigint') {
                    vm.ValueDisplaynumber = filtervalueexist?.Value1;
                    vm.ValueDisplaynumber2 = filtervalueexist?.Value2;
                }
                else if (filterdata.ColumnFieldDataCode === 'date') {
                    vm.ValueDisplaydate = filtervalueexist?.Value1;
                    vm.ValueDisplaydate2 = filtervalueexist?.Value2;
                }
                else if (filterdata.ColumnFieldDataCode === 'select' && filterdata.PickListOption === 'Lookup') {
                    if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                        var multiddl = filtervalueexist?.Value1.replace(/"|'/g, '');
                        vm.dropdwonvalues = multiddl?.split(',');
                    }
                    else {
                        vm.modelFilterDDLValue = filtervalueexist?.Value1;
                    }
                }
            }
        },
        //   Get Filter Select DDL 
        GetFilterSelectDDL(filterdata) {
            var vm = this;
            filterdata.selectOptions = [];
            var Getddldata = [];
            vm.isLoading = true;
            FilterViewReportServices.GetFilterDDLselectvalue(filterdata.CustomFieldId).then(response => {
                if (response.data.length > 0) {
                    Getddldata = response.data;
                    Getddldata.forEach(ddlitem => {
                        filterdata.selectOptions.push({
                            value: ddlitem.value,
                            label: ddlitem.name
                        })})
                    vm.isLoading = false;
                } else { vm.isLoading = false; }
            }).catch(err => {
                console.log(err)
            })
        },
        // Get oprators Data For Filter Apply on View Report 
        GetOperatordata(filterdata) {
            var vm = this;
            var CustomFieldId = filterdata.CustomFieldId;
            var params = `dataType=${filterdata.ColumnFieldDataCode}&customField=${CustomFieldId}`;
            FilterViewReportServices.GetDataOperator(params).then(response => {
                filterdata.OperatorList = response.data.DATA;
                vm.GetOperatorList = response.data.DATA;
                // filterdata.selectedOPValue = parseInt(filterdata.OPERATOR_ID);
                // vm.FillFilterValues(filterdata);
                if (filterdata.OPERATOR_ID > 0) {
                    // let operaterData = filterdata.OperatorList.find(x => x.OPERATOR_ID == filterdata.OPERATOR_ID);
                    // filterdata.OperatorExpression = operaterData.OPERATOR_EXPRESSION;
                    var checkIfExists = vm.GetOperator.filter((x) => {
                        return x.OPERATOR_ID == filterdata.OPERATOR_ID
                    });
                    if (checkIfExists.length > 0) {
                        var IsBinding = true;
                        vm.isBinding = IsBinding;
                        vm.checkvalue(filterdata, filterdata.OPERATOR_ID, checkIfExists[0].OPERATOR_DISPLAY_NAME, checkIfExists[0].OPERATOR_EXPRESSION, IsBinding)
                    }
                }
            })
        },
        applySetting(CustomFieldId, event) {
            var vm = this;
            var vm = this;
            var Multiddlvalue = [];
            vm.dropdwonvalues.forEach(ddlvalues => {
                const isddlvalueexist = vm.options.find(j => j.value === ddlvalues)
                if (isddlvalueexist != -1 && isddlvalueexist != undefined) {
                    Multiddlvalue.push(isddlvalueexist.label);
                }
            })
            if (vm.dropdwonvalues != '') {
                vm.commaSepratedSelectedValues = vm.dropdwonvalues?.join();
                vm.modelFilterDDLValue = '';
            }
            var GetDDLValue = '';
            if (vm.modelFilterDDLValue != '') {
                GetDDLValue = vm.options.find(i => i.value === vm.modelFilterDDLValue)
            }
            var elem = vm.reportListingInfo.FILTERS.find(x => x.CustomFieldId === CustomFieldId);
            elem.operator_display_name = vm.savedOperatorName;
            elem.Value1 = vm.ValueDisplaytext;
            elem.Value2 = vm.ValueDisplaytext2;
            elem.Value1 = vm.ValueDisplaynumber;
            elem.Value2 = vm.ValueDisplaynumber2;
            elem.Value1 = vm.ValueDisplaydate;
            elem.Value2 = vm.ValueDisplaydate2;
            elem.MultiDropDown = Multiddlvalue?.join();
            elem.SingleDropDown = GetDDLValue?.label;
            elem.OPERATOR_ID = vm.operator_id;
            var FirstFilterValue = '';
            var SecondFilterValue = '';
            var FirstValueforJson = '';
            var SecondValueforJson = '';

            if (vm.SelectedoperatorMappedDatatype == undefined && vm.CurrentOperatorSelected == undefined) {
                var getoperator = vm.GetOperator.find(x => x.OPERATOR_ID === parseInt(elem.OPERATOR_ID))
                vm.SelectedoperatorMappedDatatype = getoperator.MP[0].OPERATION_DATA_TYPE;
                vm.CurrentOperatorSelected = getoperator.OPERATOR_EXPRESSION;
                vm.savedOperatorName = getoperator.OPERATOR_DISPLAY_NAME;
            }

            if (elem.ColumnFieldDataCode == 'select' || elem.PickListOption == 'Lookup') {
                if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                    let CommaSepratedStringValues = vm.commaSepratedSelectedValues;
                    FirstFilterValue = CommaSepratedStringValues;
                    FirstValueforJson = CommaSepratedStringValues;
                    
                    
                }
                else {
                    FirstFilterValue = '\'' + vm.modelFilterDDLValue + '\'';
                    FirstValueforJson = vm.modelFilterDDLValue;
                }
            }

            if (elem.ColumnFieldDataCode == 'date') {
                if (elem.operator_display_name == 'Between') {
                    FirstFilterValue = '\'' + vm.ValueDisplaydate + '\'';
                    FirstValueforJson = vm.ValueDisplaydate;
                    SecondFilterValue = '\'' + vm.ValueDisplaydate2 + '\'';
                    SecondValueforJson = vm.ValueDisplaydate2;
                } else {
                    FirstFilterValue = '\'' + vm.ValueDisplaydate + '\'';
                    FirstValueforJson = vm.ValueDisplaydate;
                }
            }

            if (elem.ColumnFieldDataCode == 'string') {
                if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                    FirstFilterValue = vm.ValueDisplaytext
                    FirstValueforJson = vm.ValueDisplaytext2;

                    if (elem.operator_display_name == 'Equal to one of multiple possible values' || elem.operator_display_name == 'Not Equal to one of multiple possible values') {
                        let Commaseprated = vm.ValueDisplaytext.split(',')
                        FirstValueforJson = Commaseprated;
                    }
                }
                else {
                    FirstFilterValue = '\'' + vm.ValueDisplaytext + '\'';
                    FirstValueforJson = vm.ValueDisplaytext;
                }
            }

            if (elem.ColumnFieldDataCode == 'decimal' || elem.ColumnFieldDataCode == 'int' || elem.ColumnFieldDataCode == 'bigint') {
                if (elem.operator_display_name == 'Between') {
                    FirstFilterValue = '\'' + vm.ValueDisplaynumber + '\'';
                    FirstValueforJson = vm.ValueDisplaynumber;
                    SecondFilterValue = '\'' + vm.ValueDisplaynumber2 + '\'';
                    SecondValueforJson = vm.ValueDisplaynumber2;
                }
                else {
                    FirstFilterValue = '\'' + vm.ValueDisplaynumber + '\'';
                    SecondValueforJson = vm.ValueDisplaynumber;
                }
            }


            var checkfiledexist = vm.reportListingInfo.FILTERS.find(x => x.CustomFieldId === CustomFieldId)
            var getoperatorvalues = vm.GetOperator.find(x => x.OPERATOR_ID === checkfiledexist.selectedOPValue);
            var getexpression = vm.ConvertOperatorToExpression(getoperatorvalues.OPERATOR_EXPRESSION, FirstFilterValue, SecondFilterValue);
            checkfiledexist.OPERATOR_ID = getoperatorvalues.OPERATOR_ID;
            checkfiledexist.OperatorExpression = getexpression;
            checkfiledexist.Value1 = FirstValueforJson;
            checkfiledexist.Value2 = SecondValueforJson;
            checkfiledexist.operator_display_name = getoperatorvalues.OPERATOR_DISPLAY_NAME;
            //await vm.$parent.SetGroupDataforGraph([], vm.reportListingInfo.FILTERS);
            //vm.ClosePopFilter(event);
        },
        async ApplyNewFilterToList() {
            var vm = this;
            this.reportListingInfo.FILTERS.forEach(function(x){
                let operaterData = x.OperatorList.find(y => y.OPERATOR_ID == x.OPERATOR_ID);
                let firstValue = '\'' + x.Value1 + '\'';
                let secondValue =  '\'' + x.Value2 + '\'';
                if(x.ColumnFieldDataCode == 'select' && (operaterData.OPERATOR_EXPRESSION == 'IN' || operaterData.OPERATOR_EXPRESSION == 'NOT IN')){
                    //let valArray = x.Value1.split(",");
                    firstValue =  '\'' + x.Value1.join('\',\'') + '\''
                }
                if(x.ColumnFieldDataCode == 'decimal' || x.ColumnFieldDataCode == 'int' || x.ColumnFieldDataCode == 'bigint'){
                    x.Value1 = (x.Value1 == '') ? 0: x.Value1;
                    x.Value2 =  (x.Value2 == '') ? 0: x.Value2;
                    firstValue = x.Value1;
                    secondValue =  x.Value2;
                }
                x.OperatorExpression = vm.ConvertOperatorToExpression(operaterData.OPERATOR_EXPRESSION, firstValue, secondValue);
                x.FilterTypeAndorOr = vm.modelfiltertype;
            });
            vm.isLoading = true;
            if (vm.reportListingInfo.GRAPH.length > 0) {
                await vm.$parent.SetGroupDataforGraph([], vm.reportListingInfo.FILTERS);
            }
            vm.$parent.UpdatePreviewData([], vm.reportListingInfo.FILTERS);
            vm.isLoading = false;
            vm.close();
        },
        RemoveNewFilterFromList() {
            var vm = this;
            vm.$parent.RemoveNewFilterFromList();
            vm.close();
        },
        PreventValues(event) {
            var vm = this;
            var ekey = event.keyCode;
            if (ekey == 69 || ekey == 107 || ekey == 109) { event.preventDefault(); }
        },
        GetDropDownValueOnFilter() {
            var vm = this;
            console.log(vm.reportListingInfo.FILTERS);
            vm.reportListingInfo.FILTERS.forEach(i => {
                vm.modelfiltertype = i.FilterTypeAndorOr;
                vm.GetOperatordata(i);
                i.OperatorList = [];
                if(typeof i.isManuallyAdded === 'undefined'){
                    i.isManuallyAdded = false;
                }
                if (i.ColumnFieldDataCode == 'select' && i.PickListOption == 'Lookup') {
                    var Multiddlvalue = [];
                    var GetMultiddlvalue = [];
                    vm.GetFilterSelectDDL(i);
                    setTimeout(function () {
                        if (i.operator_display_name == 'Equal to one of multiple possible values') {
                            GetMultiddlvalue = i.Value1.split(',');
                            GetMultiddlvalue.forEach(ddlvalues => {
                                const isddlvalueexist = vm.options.find(j => j.value === ddlvalues)
                                if (isddlvalueexist != -1 && isddlvalueexist != undefined) {
                                    Multiddlvalue.push(isddlvalueexist.label);
                                }
                                i.MultiDropDown = Multiddlvalue?.join();
                            })
                        } else {
                            var GetDDLValue = '';
                            GetDDLValue = vm.options.find(i => i.value === i.Value1)
                            if(GetDDLValue !=undefined)
                                i.SingleDropDown = GetDDLValue.label;
                        }
                    }, 1000);
                }
            })
        },
        AddFilterRow: function(){
            var vm = this;
            this.$refs.FilterForm.validate().then(function(valid){
                if(valid){
                    vm.reportListingInfo.FILTERS.push({
                        "isManuallyAdded": true,
                        "CustomFieldId": "",
                        "FieldName": "",
                        "ActualDataType": "string",
                        "OperatorExpression": "",
                        "FilterTypeAndorOr": "AND",
                        "OPERATOR_ID": "",
                        "operator_display_name": "",
                        "Value1": "",
                        "Value2": "",
                        "IsMandatoryforFilter": false,
                        "FieldIcons": "",
                        "DisplayName": "",
                        "PickListOption": null,
                        "ColumnFieldDataCode": "",
                        "selectedOPValue": "",
                        "selectOptions": []
                    });
                }
            });
        },
        OnChangeFieldDdl: function(filterData){
            filterData.OperatorExpression = "";
            filterData.OPERATOR_ID = "";
            filterData.selectOptions = [];
            this.reportListingInfo.FIELDS.forEach(function(x){
                if(x.CustomFieldId == filterData.CustomFieldId){
                    filterData.DisplayName = x.DisplayName;
                    filterData.ActualDataType = x.ColumnFieldDataCode;
                    filterData.ColumnFieldDataCode = x.ColumnFieldDataCode;
                    filterData.FieldName = x.FieldName;
                    filterData.FieldIcons = x.FieldIcons;
                    filterData.FieldIcons = x.FieldIcons;
                }
            });
            this.GetOperatordata(filterData);
        },
        OnChangeOperator: function(filterData){
            var vm = this;
            filterData.selectOptions = [];
            var Getddldata = [];
            vm.isLoading = true;
            if(filterData.OPERATOR_ID > 0){
                let operaterData = filterData.OperatorList.find(x => x.OPERATOR_ID === filterData.OPERATOR_ID);
                filterData.OperatorExpression = operaterData.OPERATOR_EXPRESSION;
                if(filterData.OperatorExpression == "IN" || filterData.OperatorExpression == "NOT IN")
                {
                    filterData.Value1 = [];
                }else{
                    filterData.Value1 = "";
                }
            }
            FilterViewReportServices.GetFilterDDLselectvalue(filterData.CustomFieldId).then(response => {
                if (response.data.length > 0) {
                    Getddldata = response.data;
                    Getddldata.forEach(ddlitem => {
                        filterData.selectOptions.push({
                            value: ddlitem.value,
                            label: ddlitem.name
                        });
                    })
                    vm.isLoading = false;
                } else { vm.isLoading = false; }
            }).catch(err => {
                console.log(err)
            })
        },
        DeleteFilterRow: function(index){
            let vm = this;
            this.reportListingInfo.FILTERS.splice(index, 1);
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        }
    }
}
</script>
<template>
    <div id="modalViewCustomReportFilter" role="dialog" class="modal my-popups centre fade" v-cloak>
        <div role="document" class="modal-dialog">
            <div class="modal-content">
                <ValidationObserver ref="FilterForm"  v-slot="{ handleSubmit }">
                    <form @submit.prevent="handleSubmit(ApplyNewFilterToList)" ref="FilterForm1">
                        <div class="modal-header mheader-user">
                            <button type="button" class="close" v-on:click="close()">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 id="myModalLabel" class="modal-title">{{ $t('FILTER') }}</h4>
                        </div>
                        <div class="modal-body">
                            <loader :is-visible="isLoading"></loader>
                            <div class="list-item row filter-div">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                    <div class="form-content">                                    
                                        <div id="divshowSearchCriteriaList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                            <div class="heading-border">
                                                <h5 class="h5">
                                                    <span> Filter</span><small class="float-right">
                                                    <a href="javascript:;" style="cursor: pointer;" title="Add" v-on:click="AddFilterRow()"><em class="fa fa-plus pr-2"></em> Add </a></small>
                                                </h5>
                                            </div>
                                            <div class="listing mt-2">
                                                <div class="mb-2">
                                                    <label>Include data if it matches</label>
                                                <select class="form-control col-4" v-model="modelfiltertype">
                                                    <option v-for="filtertype in FilterCategory" :value="filtertype">{{ filtertype }}</option>
                                                </select>
                                                </div>
                                                <div class="table-responsive overflow-visible">
                                                    <table aria-describedby="serachfilter" class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                <th scope="col" style="min-width: 180px;"><span>Name</span></th>
                                                                <th scope="col" style="min-width: 180px;"><span>Operator</span></th>
                                                                <th scope="col" style="min-width: 480px;"><span>Value</span></th>
                                                                <th scope="col" style="min-width: 50px;"><span>Action</span></th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(filterdata, index) in reportListingInfo.FILTERS" v-if="reportListingInfo.FILTERS.length > 0">
                                                                <td>
                                                                    <span v-if="!filterdata.isManuallyAdded">{{ GetLocalizedValue(filterdata.FieldName, filterdata.DisplayName) }}</span>
                                                                    <Field :name="'Field'+index" v-slot="{ field, errors, errorMessage, meta }" rules="required" v-if="filterdata.isManuallyAdded">
                                                                        <select :class="{'form-control':true, 'is-invalid': errors[0]}" v-on:change="OnChangeFieldDdl(filterdata)"  v-model="filterdata.CustomFieldId">
                                                                            <option value="">Select field</option>
                                                                            <option :value="item.CustomFieldId" v-for="item in reportListingInfo.FIELDS"
                                                                            :disabled="(reportListingInfo.FILTERS.filter(x=> x.CustomFieldId == item.CustomFieldId)).length > 0">{{  GetLocalizedValue(item.FieldName, item.DisplayName) }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>   
                                                                <td>
                                                                    <!-- {{ filterdata.operator_display_name }} -->
                                                                    <Field :name="'Operator'+index" v-slot="{ field, errors, errorMessage }" rules="required" >
                                                                        <select :class="{'form-control':true, 'is-invalid': errors[0]}" v-model="filterdata.OPERATOR_ID" v-on:change="OnChangeOperator(filterdata)">
                                                                            <option value="">Select Operator</option>
                                                                            <option v-for="item in GetOperatorList" :value="item.OPERATOR_ID">{{ item.OPERATOR_DISPLAY_NAME }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>
                                                                <td :style="((filterdata.ActualDataType == 'select' && (filterdata.OperatorExpression == 'IN' || filterdata.OperatorExpression == 'NOT IN') ? 'display: contents;' : ''))">
                                                                    <span v-if="filterdata.OperatorExpression == 'Is Not Null' || filterdata.OperatorExpression == 'Is Null'">--</span>
                                                                    
                                                                    <Field :name="'StringFirstValue'+index" v-slot="{ field, errors, errorMessage }" rules="required"  v-if="filterdata.ActualDataType == 'string' && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input type="text" id="inpttxt" :class="{'form-control':true, 'is-invalid': errors[0]}"  v-model="filterdata.Value1" :style="(filterdata.OperatorExpression == 'between'?'width: 45%;float: left;': '')" />
                                                                    </Field>

                                                                    <Field :name="'StringSecondValue'+index" v-slot="{ field, errors, errorMessage }" rules="required"  v-if="filterdata.ActualDataType == 'string' && filterdata.OperatorExpression == 'between'">
                                                                        <input  type="text" id="inpttxt2" :class="{'form-control':true, 'is-invalid': errors[0]}"  v-model="filterdata.Value2" style="width: 45%;float: left;margin-left: 50px;" />
                                                                    </Field>

                                                                    <Field :name="'NumberFirstValue'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="(filterdata.ActualDataType == 'decimal' || filterdata.ActualDataType == 'int' || filterdata.ActualDataType == 'bigint') && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input  type="number" id="inpttxtbtw" :class="{'form-control':true, 'is-invalid': errors[0]}" :style="(filterdata.OperatorExpression == 'between'?'width: 45%;float: left;': '')" @keydown="PreventValues($event)" v-model="filterdata.Value1"/>
                                                                    </Field>
                                                                    
                                                                    <Field :name="'NumberSecondValue'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="(filterdata.ActualDataType == 'decimal' || filterdata.ActualDataType == 'int' || filterdata.ActualDataType == 'bigint') && filterdata.OperatorExpression == 'between'">
                                                                        <input  type="number" id="inpttxtbtw" :class="{'form-control':true, 'is-invalid': errors[0]}" style="width: 45%;float: left;margin-left: 50px;" @keydown="PreventValues($event)" v-model="filterdata.Value2"/>
                                                                    </Field>

                                                                    <Field :name="'DateFirstValue'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="(filterdata.ActualDataType == 'date' || filterdata.ActualDataType == 'datetime') && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <input  type="date" id="inpttxtdate1" :class="{'form-control':true, 'is-invalid': errors[0]}" :style="(filterdata.OperatorExpression == 'between'?'width: 45%;float: left;': '')" v-model="filterdata.Value1"/>
                                                                    </Field>
                                                                    
                                                                    <Field :name="'DateSecondValue'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="(filterdata.ActualDataType == 'date' || filterdata.ActualDataType == 'datetime') && filterdata.OperatorExpression == 'between'">
                                                                        <input type="date" :min="filterdata.Value1" :class="{'form-control':true, 'is-invalid': errors[0]}" style="width: 45%;float: left;margin-left: 20px;" v-model="filterdata.Value2"/>
                                                                    </Field>

                                                                    <Field :name="'Selectddl'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="filterdata.ActualDataType == 'select' && filterdata.OperatorExpression != 'IN' && filterdata.OperatorExpression != 'NOT IN' && filterdata.OperatorExpression != 'Is Not Null' && filterdata.OperatorExpression != 'Is Null'">
                                                                        <select :class="{'form-control':true, 'is-invalid': errors[0]}"  v-model="filterdata.Value1">
                                                                            <option value="">Select value</option>
                                                                            <option v-for="DDLitem in filterdata.selectOptions" :key="DDLitem.value" :value="DDLitem.value">
                                                                                {{ DDLitem.label }}
                                                                            </option>
                                                                        </select>
                                                                    </Field>
                                                                    
                                                                    <Field :name="'MultiSelectddl'+index" v-slot="{ field, errors, errorMessage }" rules="required" v-if="filterdata.ActualDataType == 'select' && (filterdata.OperatorExpression == 'IN' || filterdata.OperatorExpression == 'NOT IN')">
                                                                        <div>
                                                                            <!-- <Multiselect v-bind="field" :class="{'form-control':true, 'is-invalid': errorMessage}" mode="tags" :searchable="true" :taggable="false" :close-on-select="false" :options="filterdata.selectOptions" /> -->
                                                                            <Multiselect                                                                          
                                                                                :class="{'form-control':true, 'is-invalid': errors[0]}"
                                                                                v-model="filterdata.Value1"
                                                                                :multiple="true"                                                                                                
                                                                                label="label" 
                                                                                track-by="label"
                                                                                placeholder="Select"
                                                                                :searchable="true"
                                                                                mode="multiple"
                                                                                :hideSelected="false"
                                                                                :close-on-select="false"
                                                                                :options="filterdata.selectOptions">
                                                                                <template v-slot:multiplelabel="{ values }">
                                                                                    <div class="multiselect-multiple-label" v-if="values && values.length>0">
                                                                                        <div class="multiselect-tags" data-tags="">
                                                                                            <span class="multiselect-tag" tabindex="-1" :aria-label="values[0].label">{{ TruncateStr(values[0].label,18) }} 
                                                                                                <span class="multiselect-tag-remove">
                                                                                                    <span class="multiselect-tag-remove-icon"></span>
                                                                                                </span>
                                                                                            </span>
                                                                                        </div>
                                                                                        <span v-if="values && values.length>1">
                                                                                        {{ values.length }} options selected
                                                                                        </span>
                                                                                    </div>                                                                                                                                                                          
                                                                                </template>
                                                                            </Multiselect>
                                                                        </div>
                                                                    </Field>
                                                                </td>
                                                                <td class="text-center">
                                                                    <a href="javascript:;" v-on:click="DeleteFilterRow(index)"><em v-if="filterdata.isManuallyAdded" class="fa fa-trash text-danger pr-2"></em></a>
                                                                    <span v-if="!filterdata.isManuallyAdded">--</span>
                                                                </td>
                                                            </tr>
                                                            <tr  v-if="reportListingInfo.FILTERS.length == 0">
                                                                <td colspan="10" class="text-center text-danger py-3">No record(s) found</td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 mb-3 mr-auto d-flex align-items-center justify-content-end">
                            <button type="submit" class="btn btn-success my-2 my-sm-0 mr-2 pl-1"> <span
                                    class="fa fa-search"></span> Search</button>
                            <a class="btn btn-danger pl-1 mb-0" v-on:click="RemoveNewFilterFromList()">  <span
                                    class="fa fa-times"></span> Reset Filter</a>
                        </div>
                    </form>
                </ValidationObserver>
            </div>
        </div>
    </div>
</template>

<style scoped>
.width80 {
    width: 90px;
}

.width120 {
    width: 130px;
}
</style>
